import * as React from "react";
import styled from "styled-components";
import { autobind } from "core-decorators";
import { inject, observer } from "mobx-react";
import { Provider } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router";

import { stores } from "./modules";
import { ISearchStore } from "./modules/store/SearchStore";
import { blockColor } from "assets/colors";
import SearchItem from "./molecules/SearchItem";
import backImg from "assets/img/back_icon.png";

type Props = RouteComponentProps & {
  SearchStore?: ISearchStore;
};

@inject("SearchStore")
@observer
@autobind
class SearchPage extends React.Component<Props> {
  state = {
    searchText: ""
  };

  render() {
    return (
      <Container>
        <Header>
          <BackIcon
            src={backImg}
            onClick={() => {
              this.props.history.goBack();
            }}
          />
          <SearchInput
            placeholder={" . EX ) "}
            onChange={this.onChangeText}
            value={this.state.searchText}
          />
        </Header>
        <ItemContainer>
          {this.props.SearchStore!.searchResults.map((result, idx) => {
            return (
              <SearchItem
                key={idx}
                text={result.regionName}
                onClick={() => {
                  //  
                  // localStorage.getItem("dust_uuid");
                  this.props.history.goBack();
                }}
              />
            );
          })}
        </ItemContainer>
      </Container>
    );
  }

  onChangeText = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ searchText: e.target.value });
    this.props.SearchStore!.search(e.target.value);
  };
}

const Container: React.ComponentType<any> = styled.div`
  background: #f7f7f7;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
`;

const Header = styled.div`
  position: absolute;
  top: 0;
  display: flex;
  width: 100%;
  height: 6.5rem;
  background: ${blockColor};
  margin-bottom: 0.625rem;
`;

const BackIcon = styled.img`
  width: 2.25rem;
  height: 2.25rem;
  margin-left: 0.625rem;
  margin-top: 3.375rem;
`;

const SearchInput = styled.input`
  width: calc(100% - 6.25rem);
  margin-top: 3.375rem;
  margin-left: 0.25rem;
  height: 2.25rem;
  background: ${blockColor};
  font-size: 18px;
  border: none;
  color: #111111;
  font-family: "Noto Sans KR", sans-serif;
  ::placeholder {
    color: white;
  }
`;

const ItemContainer = styled.div`
  width: 100%;
  margin-top: 6.5rem;
`;

class SearchPageWithStore extends React.Component<Props> {
  searchPageWithRouter = withRouter(SearchPage);

  public render() {
    return (
      <Provider {...stores}>
        <this.searchPageWithRouter />
      </Provider>
    );
  }
}

export default SearchPageWithStore;



