import { PhaseType } from "common/types";

export default class DustHelpers {
  static getPhase(pm10: number): PhaseType {
    if (pm10 <= 30) {
      return 1;
    } else if (pm10 <= 80) {
      return 2;
    } else if (pm10 <= 150) {
      return 3;
    } else {
      return 4;
    }
  }

  static getPhaseString(phase: PhaseType) {
    switch (phase) {
      case 1:
        return "";
      case 2:
        return "";
      case 3:
        return "";
      case 4:
        return "";
      default:
        return "";
    }
  }

  static getDescription(phase: PhaseType) {
    switch (phase) {
      case 1:
        return "   .";
      case 2:
        return ",    .";
      case 3:
        return " ,   .";
      case 4:
        return "    .";
      default:
        return "";
    }
  }
}



