import axios from "axios";
import { env } from "helpers/config";

export const getDustInfoList = async (long: number, lat: number) => {
  return await axios.get(`${env.API_URL}/info/${long},${lat}`);
};

export const login = async (uuid: string, fcmToken: string) => {
  return await axios.put(
    `${env.API_URL}/users/token`,
    {
      firebaseToken: fcmToken
    },
    {
      headers: {
        Authorization: uuid
      }
    }
  );
};



