import * as React from "react";
import styled from "styled-components";
import { backgroundColor } from "assets/colors";
import ButtonIcon from "../molecules/ButtonIcon";
import ButtonLabel from "../molecules/ButtonLabel";

type Props = {
  isMaskRegistered: boolean;
  status: 1 | 2 | 3;
};

const MaskButton: React.FC<Props> = (props: Props) => {
  return (
    <Container>
      <ButtonIcon
        status={props.status}
        isMaskRegistered={props.isMaskRegistered}
      />
      <ButtonLabel
        status={props.status}
        isMaskRegistered={props.isMaskRegistered}
      />
    </Container>
  );
};

const Container: React.ComponentType<any> = styled.div`
  width: 53%;
  margin-top: 2.8125rem;
  margin-right: auto;
  margin-left: auto;
  background-color: ${(props: any) =>
    props.isMaskRegistered ? `${backgroundColor}` : "#000000"};
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  padding-right: 1.875rem;
  padding-left: 1.875rem;
`;

export default MaskButton;



