import * as React from "react";
import styled from "styled-components";
import { withRouter } from "react-router";
import { RouteComponentProps } from "react-router";

import MaskImage from "assets/img/mask_img.png";
import { getMaskButtonColor } from "assets/colors";
import { PhaseType } from "common/types";

type Props = RouteComponentProps & {
  phase: PhaseType;
  isExpandedMode: boolean;
};

class MaskButton extends React.Component<Props> {
  render() {
    return (
      <Container
        phase={this.props.phase}
        isExpandedMode={this.props.isExpandedMode}
        onClick={() => {
          this.props.history.push("/mask");
        }}
      >
        <MaskIcon src={MaskImage} isExpandedMode={this.props.isExpandedMode} />
      </Container>
    );
  }
}

const Container: React.ComponentType<any> = styled.div`
  position: absolute;
  display: flex;
  width: ${(props: any) => (props.isExpandedMode ? 4.25 : 2.75)}rem;
  height: ${(props: any) => (props.isExpandedMode ? 4.25 : 2.75)}rem;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.22);
  top: ${(props: any) => (props.isExpandedMode ? 11.125 : 4.35)}rem;
  right: 1.25rem;
  background: ${(props: any) => getMaskButtonColor(props.phase)};
  transition: top 0.5s, width 0.5s, height 0.5s;
`;

const MaskIcon: React.ComponentType<any> = styled.img`
  width: 85%;
`;

export default withRouter(MaskButton);



