import * as React from "react";
import styled from "styled-components";

import AddIconImage from "assets/img/add_icon.png";
import PlayIconImage from "assets/img/play_icon.png";
import StopIconImage from "assets/img/stop_icon.png";

type Props = {
  isMaskRegistered: boolean;
  status: 1 | 2 | 3;
};

const ButtonIcon: React.FC<Props> = (props: Props) => {
  if (props.isMaskRegistered) {
    switch (props.status) {
      case 1:
        return <Icon src={StopIconImage} />;
      case 2:
        return <Icon src={PlayIconImage} />;
      case 3:
        return <Icon src={PlayIconImage} />;
      default:
        return <Icon src={AddIconImage} />;
    }
  } else {
    return <Icon src={AddIconImage} />;
  }
};

const Icon = styled.img`
  width: 20px;
  height: 20px;
`;

export default ButtonIcon;



