import _ from "lodash";

let _env = process.env;
_env.debug = _.toString(_env.NODE_ENV !== "production");
const prefix = "REACT_APP_";
const stripReact = (key: String) => key.replace(prefix, "");

export const env = _.zipObject(_.map(_.keys(_env), stripReact), _.values(_env));



