import * as React from "react";
import styled from "styled-components";

import FigureCard from "../molecules/FigureCard";
import { subTitleColor } from "assets/colors";
import FigureGage from "../molecules/FigureGage";
import { PhaseType } from "common/types";

type Props = {
  location: string;
  isCurrentLocation?: boolean;
  dustState: string;
  description: string;
  fineDust: number;
  ultrafineDust: number;
  phase: PhaseType;
};

const WeatherCard: React.FC<Props> = (props: Props) => {
  return (
    <Container href={"/#/detail"}>
      <Location>{props.location}</Location>
      {props.isCurrentLocation ? (
        <CurrentLocationText></CurrentLocationText>
      ) : null}
      <StateBlock>
        <DustState>{props.dustState}</DustState>
        <FigureGage phase={props.phase} />
      </StateBlock>
      <Description>{props.description}</Description>
      {props.isCurrentLocation ? (
        <FigureBox>
          <FigureCard title="" data={`${props.fineDust} /m`} />
          <FigureCard
            title=""
            data={`${props.ultrafineDust} /m`}
          />
        </FigureBox>
      ) : null}
    </Container>
  );
};

const Container = styled.a`
  position: relative;
  width: 100%;
  background: white;
  padding: 1.25rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.26);
  color: #111111;
  text-decoration: none;
  a:active {
    text-decoration: none;
  }
  a:visited {
    text-decoration: none;
  }
`;

const Location = styled.p`
  position: absolute;
  top: 0.875rem;
  right: 1.25rem;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

const CurrentLocationText = styled.p`
  position: absolute;
  top: 2.25rem;
  right: 1.25rem;
  font-size: 14px;
  color: ${subTitleColor};
  font-family: "Noto Sans KR", sans-serif;
`;

const StateBlock = styled.div`
  display: flex;
  margin-top: 3.125rem;
  align-items: center;
`;

const DustState = styled.p`
  font-size: 36px;
  margin-right: 0.625rem;
`;

const Description = styled.p`
  margin-top: 0.375rem;
`;

const FigureBox = styled.div`
  width: 100%;
  margin-top: 0.125rem;
`;

export default WeatherCard;



