import * as React from "react";
import styled from "styled-components";
import CircleProgressBar from "../molecules/CircleProgressBar";

type Props = {
  isMaskRegistered: boolean;
};

const UsageStateSection: React.FC<Props> = (props: Props) => {
  return (
    <Container>
      <CircleProgressBar />
      <ProgressTitle>
        { props.isMaskRegistered ? `\n !` : `\n.`}
      </ProgressTitle>
      <ProgressTime> 06:30:25</ProgressTime>
    </Container>
  );
};

const Container = styled.div`
  width: 300px;
  height: 300px;
  position: relative;
`;

const ProgressTitle = styled.span`
  text-align: center;
  position: absolute;
  font-size: 30px;
  color: #1a1b1d;
  top: 30%;
  left: 24%;
  white-space: pre-line;
`;

const ProgressTime = styled.p`
  text-align: center;
  position: absolute;
  opacity: 0.68;
  font-size: 18px;
  top: 190px;
  left: 75px;
`;

export default UsageStateSection;



