import * as React from "react";
import styled from "styled-components";

import { boxColor, getBackgroundColor } from "assets/colors";
import { PhaseType } from "common/types";

type Props = {
  title: string;
  data: string;
  phase: PhaseType;
};

const FigureCard: React.FC<Props> = (props: Props) => {
  return (
    <Container phase={props.phase}>
      <Title>{props.title}</Title>
      <Data>{props.data}</Data>
    </Container>
  );
};

const Container: React.ComponentType<any> = styled.div`
  background: ${(props: any) => {
    if (props.phase) {
      return getBackgroundColor(props.phase);
    } else {
      return boxColor;
    }
  }};
  color: ${(props: any) => {
    if (props.phase === 4) {
      return "#eeeeee";
    } else {
      return "#000000";
    }
  }};
  display: inline-block;
  padding: 0.375rem 0.625rem;
  margin-right: 0.625rem;
  margin-top: 0.625rem;
`;

const Title = styled.p`
  display: inline-block;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

const Data = styled.p`
  display: inline-block;
  margin-left: 0.375rem;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

export default FigureCard;



