import * as React from "react";
import styled, { keyframes } from "styled-components";

import FirstPhaseCircleImg from "assets/img/circle_level1.png";
import SecondPhaseCircleImg from "assets/img/circle_level2.png";
import ThirdPhaseCircleImg from "assets/img/circle_level3.png";
import FourthPhaseCircleImg from "assets/img/circle_level4.png";
import BlackLineImg from "assets/img/line_black.png";
import WhiteLineImg from "assets/img/line_white.png";
import {
  firstPhaseColor,
  secondPhaseColor,
  thirdPhaseColor,
  fourthPhaseColor
} from "assets/colors";
import { PhaseType } from "common/types";

type Props = {
  phase: PhaseType;
};

const AnimationBackground: React.FC<Props> = (props: Props) => {
  const getBackground = () => {
    switch (props.phase) {
      case 1:
        return (
          <AnimationContainer background={firstPhaseColor}>
            <FirstPhaseCircle src={FirstPhaseCircleImg} />
            <FirstPhaseCircle2 src={FirstPhaseCircleImg} />
            <FirstPhaseCircle3 src={FirstPhaseCircleImg} />
          </AnimationContainer>
        );
      case 2:
        return (
          <AnimationContainer background={secondPhaseColor}>
            <SecondPhaseCircle src={SecondPhaseCircleImg} />
            <SecondPhaseCircle2 src={SecondPhaseCircleImg} />
            <SecondPhaseCircle3 src={SecondPhaseCircleImg} />
            <SecondPhaseCircle4 src={SecondPhaseCircleImg} />
          </AnimationContainer>
        );
      case 3:
        return (
          <AnimationContainer background={thirdPhaseColor}>
            <ThirdPhaseCircle src={ThirdPhaseCircleImg} />
            <ThirdPhaseCircle2 src={ThirdPhaseCircleImg} />
            <ThirdPhaseCircle3 src={ThirdPhaseCircleImg} />
            <ThirdPhaseCircle4 src={ThirdPhaseCircleImg} />
          </AnimationContainer>
        );
      case 4:
        return (
          <AnimationContainer background={fourthPhaseColor}>
            <FourthPhaseCircle src={FourthPhaseCircleImg} />
            <FourthPhaseCircle2 src={FourthPhaseCircleImg} />
            <FourthPhaseCircle3 src={FourthPhaseCircleImg} />
            <FourthPhaseCircle4 src={FourthPhaseCircleImg} />
          </AnimationContainer>
        );
      default:
        return null;
    }
  };

  const getLineImg = () => {
    switch (props.phase) {
      case 1:
        return (
          <>
            <LeftLine src={BlackLineImg} />
            <RightLine src={BlackLineImg} />
          </>
        );
      case 2:
        return (
          <>
            <LeftLine src={BlackLineImg} />
            <RightLine src={BlackLineImg} />
          </>
        );
      case 3:
        return (
          <>
            <LeftLine src={WhiteLineImg} />
            <RightLine src={WhiteLineImg} />
          </>
        );
      case 4:
        return (
          <>
            <LeftLine src={WhiteLineImg} />
            <RightLine src={WhiteLineImg} />
          </>
        );
      default:
        return null;
    }
  };

  return (
    <Container>
      {getBackground()}
      {getLineImg()}
    </Container>
  );
};

const Container = styled.div`
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
`;

const LeftLine = styled.img`
  position: absolute;
  margin-left: -3rem;
  margin-top: 13.4375rem;
`;

const RightLine = styled.img`
  position: absolute;
  margin-left: 3rem;
`;

const AnimationContainer: React.ComponentType<any> = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  background: ${(props: any) => props.background};
`;

const mymove = keyframes`
  0% {
    right: -10rem;
  }
  100% {
    right: 110%;
  }
`;

const FirstPhaseCircle = styled.img`
  position: absolute;
  top: 0.875rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -14s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -14s;
  -webkit-animation-duration: 20s;
`;

const FirstPhaseCircle2 = styled.img`
  position: absolute;
  top: 10rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -1s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -1s;
  -webkit-animation-duration: 20s;
`;

const FirstPhaseCircle3 = styled.img`
  position: absolute;
  bottom: -2rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -10s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -10s;
  -webkit-animation-duration: 20s;
`;

const SecondPhaseCircle = styled.img`
  position: absolute;
  top: 0.8125rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -10s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -10s;
  -webkit-animation-duration: 20s;
`;

const SecondPhaseCircle2 = styled.img`
  position: absolute;
  top: 4.3125rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -1s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -1s;
  -webkit-animation-duration: 20s;
`;

const SecondPhaseCircle3 = styled.img`
  position: absolute;
  top: 6rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -14s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -14s;
  -webkit-animation-duration: 20s;
`;

const SecondPhaseCircle4 = styled.img`
  position: absolute;
  bottom: 3rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -11s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -11s;
  -webkit-animation-duration: 20s;
`;

const ThirdPhaseCircle = styled.img`
  position: absolute;
  top: 6.125rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -14s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -14s;
  -webkit-animation-duration: 20s;
`;

const ThirdPhaseCircle2 = styled.img`
  position: absolute;
  top: -1.5rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -1s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -1s;
  -webkit-animation-duration: 20s;
`;

const ThirdPhaseCircle3 = styled.img`
  position: absolute;
  top: 10.875rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -10s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -10s;
  -webkit-animation-duration: 20s;
`;

const ThirdPhaseCircle4 = styled.img`
  position: absolute;
  bottom: 3rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -11s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -11s;
  -webkit-animation-duration: 20s;
`;

const FourthPhaseCircle = styled.img`
  position: absolute;
  top: 6.125rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -14s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -14s;
  -webkit-animation-duration: 20s;
`;

const FourthPhaseCircle2 = styled.img`
  position: absolute;
  top: -1.5rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -1s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -1s;
  -webkit-animation-duration: 20s;
`;

const FourthPhaseCircle3 = styled.img`
  position: absolute;
  top: 10.875rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -10s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -10s;
  -webkit-animation-duration: 20s;
`;

const FourthPhaseCircle4 = styled.img`
  position: absolute;
  bottom: 3rem;
  animation: ${mymove} 5s linear infinite;
  animation-delay: -11s;
  animation-duration: 20s;
  -webkit-animation: ${mymove} 5s linear infinite;
  -webkit-animation-delay: -11s;
  -webkit-animation-duration: 20s;
`;

export default AnimationBackground;



