import * as React from "react";
import styled from "styled-components";
import { textColor } from "assets/colors";

type Props = {
  isMaskRegistered: boolean;
  status: 1 | 2 | 3;
};

const ButtonLabel: React.FC<Props> = (props: Props) => {
  if (props.isMaskRegistered) {
    switch (props.status) {
      case 1:
        return <Text>  </Text>;
      case 2:
        return <Text>  </Text>;
      case 3:
        return <Text>  </Text>;
      default:
        return <Text>  </Text>;
    }
  } else {
    return <Text> </Text>;
  }
};

const Text: React.ComponentType<any> = styled.p`
  display: block;
  color: ${(props: any) => (props.isMaskRegistered ? `${textColor}` : "white")};
  margin-left: 0.625rem;
  font-size: 15px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
`;

export default ButtonLabel;



