import React, { createRef } from "react";
import styled from "styled-components";

type Props = {};
type State = {
  acrInterval: number;
};

class CircleProgressBar extends React.Component<Props, State> {
  private canvasRef = createRef<HTMLCanvasElement>();

  private posX = 0;
  private posY = 0;
  private fps = 0;
  private onePercent = 0;
  private result = 0;

  render() {
    return <Canvas ref={this.canvasRef} width={600} height={600} />;
  }

  componentDidMount() {
    const circle = this.canvasRef.current!.getContext("2d");

    this.posX = this.canvasRef.current!.width / 2;
    this.posY = this.canvasRef.current!.height / 2;
    this.fps = 1000 / 200;
    this.onePercent = 360 / 100;
    this.result = this.onePercent * 64;

    circle!.lineCap = "round";

    this.arcMove(circle!);
  }

  componentWillUnmount() {
    clearInterval(this.state.acrInterval);
  }

  arcMove(circle: any) {
    const self = this;
    const c = circle;

    let deegres = 0;
    let acrInterval = setInterval(function() {
      deegres += 1;
      c.clearRect(
        0,
        0,
        self.canvasRef.current!.width,
        self.canvasRef.current!.height
      );

      c.beginPath();
      c.arc(
        self.posX,
        self.posY,
        290,
        (Math.PI / 180) * 270,
        (Math.PI / 180) * (270 + 360)
      );
      c.strokeStyle = "#979797";
      c.lineWidth = 3;
      c.stroke();

      c.beginPath();
      c.strokeStyle = "#979797";
      c.lineWidth = 15;
      c.arc(
        self.posX,
        self.posY,
        290,
        (Math.PI / 180) * 270,
        (Math.PI / 180) * (270 + deegres)
      );
      c.stroke();

      if (deegres >= self.result) clearInterval(acrInterval);
    }, self.fps);

    this.setState({ acrInterval });
  }
}

const Canvas = styled.canvas`
  width: 300px;
  height: 300px;
`;

export default CircleProgressBar;



