import { observable, action } from "mobx";
import { autobind } from "core-decorators";
import Mask from "../model/Mask";

export interface IMaskStore {
  mask: Mask;
  setStartTime: () => void;
}

@autobind
export class MaskStore implements IMaskStore {
  @observable
  mask: Mask = new Mask();

  @action
  public async setStartTime() {
    this.mask = new Mask();
  }
}



