import * as React from "react";
import styled from "styled-components";

import { getGageBlockColor, backgroundColor } from "assets/colors";
import { PhaseType } from "common/types";

type Props = {
  phase: PhaseType;
};

const FigureGage: React.FC<Props> = (props: Props) => {
  return (
    <Container>
      <GageBlock isOn={props.phase >= 4} phase={props.phase} />
      <GageBlock isOn={props.phase >= 3} phase={props.phase} />
      <GageBlock isOn={props.phase >= 2} phase={props.phase} />
      <GageBlock isOn={props.phase >= 1} phase={props.phase} />
    </Container>
  );
};

const Container = styled.div`
  display: grid;
  grid-auto-columns: auto;
  grid-row-gap: 0.16875rem;
  margin-left: 0.625rem;
`;

const GageBlock: React.ComponentType<any> = styled.div`
  background: ${(props: any) => {
    if (props.isOn) {
      return getGageBlockColor(props.phase);
    } else {
      return backgroundColor;
    }
  }};
  width: 1.25rem;
  height: 0.25rem;
`;

export default FigureGage;



