import * as React from "react";
import styled from "styled-components";

type Props = {
  name: string;
  numerical: number;
};

const Figure: React.FC<Props> = (props: Props) => {
  return (
    <Container>
      <Name>{props.name}</Name>
      <Numerical>{props.numerical}</Numerical>
    </Container>
  );
};

const Container = styled.div`
  display: block;
`;

const Name = styled.p`
  text-align: center;
  font-size: 12px;
  font-family: "Noto Sans KR", sans-serif;
`;

const Numerical = styled.p`
  margin-top: 5px;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
`;

export default Figure;



