import * as React from "react";
import styled from "styled-components";
import { autobind } from "core-decorators";
import { inject, observer } from "mobx-react";
import { Provider } from "mobx-react";

import { stores } from "./modules";
import FigureCard from "./molecules/FigureCard";
import FigureTable from "./organisms/FigureTable";
import DetailInfoTable from "./organisms/DetailInfoTable";
import FigureGage from "./molecules/FigureGage";
import { IDetailStore } from "./modules/store/DetailStore";
import BackIconImg from "assets/img/back_icon.png";
import LocationIconImg from "assets/img/location_icon.png";
import { getBackgroundColor, subTitleColor, boxColor } from "assets/colors";

type Props = {
  DetailStore?: IDetailStore;
};

@inject("DetailStore")
@observer
@autobind
class DetailPage extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
  }

  render() {
    return (
      <Container background={getBackgroundColor}>
        <BackButton src={BackIconImg} />
        <LocationContainer>
          <LocationIcon src={LocationIconImg} />
          <Location>  </Location>
        </LocationContainer>
        <WeatherInfo> : 17 |  : 19%</WeatherInfo>
        <StateBlock>
          <DustState></DustState>
          <FigureGage phase={4} />
        </StateBlock>
        <DustStateDescription>
          {this.setDustStateDescription(4)}
        </DustStateDescription>
        <FigureBox>
          <FigureCard title="" data={`50 /m`} phase={4} />
          <FigureCard title="" data={`26 /m`} phase={4} />
        </FigureBox>

        <FigureDetailBox>
          <FigureDetailTitle>   </FigureDetailTitle>
          <FigureTable />
          <DetailInfoTitle> </DetailInfoTitle>
          <DetailInfoTable />
          <BoxDiscription>
                    
                    
               {" "}
          </BoxDiscription>
        </FigureDetailBox>

        <DataReferBox>
          <DataReferTitle> </DataReferTitle>
          <BoxDiscription>
                    
                    
               
          </BoxDiscription>
        </DataReferBox>
      </Container>
    );
  }

  componentDidMount() {
    this.props.DetailStore!.loadStartTime();
  }

  setDustStateDescription(phase: number) {
    switch (phase) {
      case 1:
        return "  .";
      case 2:
        return "    .";
      case 3:
        return " ,   .";
      case 4:
        return "    .";
    }
    return "";
  }
}

const Container: React.ComponentType<any> = styled.div`
  background: ${(props: any) => props.background};
  width: 100%;
  height: 100%;
  overflow-y: scroll;
`;

const BackButton = styled.img`
  margin-left: 0.625rem;
  margin-top: 1.25rem;
  width: 36px;
  height: 36px;
`;

const LocationContainer = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 1.25rem;
`;

const LocationIcon = styled.img`
  width: 18px;
  height: 18px;
`;

const Location = styled.p`
  margin-left: 2px;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

const WeatherInfo = styled.p`
  text-align: end;
  margin-top: 0.1rem;
  margin-right: 1.25rem;
  font-size: 14px;
  color: ${subTitleColor};
  font-family: "Noto Sans KR", sans-serif;
`;

const StateBlock = styled.div`
  display: flex;
  margin-top: 3.125rem;
  align-items: center;
  margin-left: 1.875rem;
`;

const DustState = styled.p`
  font-size: 30px;
`;

const DustStateDescription = styled.p`
  margin-top: 0.625rem;
  margin-left: 1.875rem;
`;

const FigureBox = styled.div`
  width: 100%;
  margin-top: 0.125rem;
  margin-left: 1.875rem;
`;

const FigureDetailBox = styled.div`
  width: auto;
  margin-top: 4.65rem;
  margin-left: 0.625rem;
  margin-right: 0.625rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  background: ${boxColor};
`;

const FigureDetailTitle = styled.p`
  text-align: center;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

const DetailInfoTitle = styled.p`
  margin-top: 1.25rem;
  text-align: center;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

const BoxDiscription = styled.p`
  margin-top: 0.95rem;
  color: #1a1b1d;
  font-size: 10px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
`;

const DataReferBox = styled.div`
  margin-top: 1px;
  width: auto;
  margin-left: 0.625rem;
  margin-right: 0.625rem;
  margin-bottom: 13px;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  background: ${boxColor};
`;

const DataReferTitle = styled.p`
  text-align: center;
  font-size: 14px;
  font-family: "Noto Sans KR", sans-serif;
`;

class DetailPageWithStore extends React.Component<Props> {
  public render() {
    return (
      <Provider {...stores}>
        <DetailPage />
      </Provider>
    );
  }
}

export default DetailPageWithStore;



