import React, { useState } from "react";
import styled from "styled-components";

import { floatingBoxColor } from "assets/colors";

type Props = {
  duration: number;
};

const BottomFloatingBox: React.FC<Props> = props => {
  const [isOn, setIsOn] = useState(true);

  return (
    <Container>
      <DurationText>{`  ${props.duration} `}</DurationText>
      <SwitchButtonContainer isOn={isOn}>
        <SwitchButton
          isOn={isOn}
          onClick={() => {
            setIsOn(!isOn);
          }}
        >
          {isOn ? "On" : "Off"}
        </SwitchButton>
      </SwitchButtonContainer>
    </Container>
  );
};

const Container = styled.div`
  position: absolute;
  display: flex;
  bottom: 0;
  width: 100%;
  height: 4.375rem;
  background: ${floatingBoxColor};
  align-items: center;
`;

const DurationText = styled.p`
  margin-left: 1.625rem;
  font-family: "Noto Sans KR", sans-serif;
`;

const SwitchButtonContainer: React.ComponentType<any> = styled.div`
  position: absolute;
  width: 4rem;
  height: 1.875rem;
  right: 1.625rem;
  background: ${(props: any) => (props.isOn ? "#7297b2" : "#454545")};
  border-radius: 8px;
`;

const SwitchButton: React.ComponentType<any> = styled.button`
  width: 2.8125rem;
  height: 1.875rem;
  font-family: "Noto Sans KR", sans-serif;
  border: none;
  background: white;
  border-radius: 8px;
  transform: ${(props: any) =>
    props.isOn ? "translateX(1.25rem)" : "translateX(0)"};
  transition: transform 0.3s;
  font-weight: normal;
`;

export default BottomFloatingBox;



