import { observable, action } from "mobx";
import { autobind } from "core-decorators";
import DustInfo from "../model/DustInfo";
import { getDustInfoList, login } from "../adapter/MainAdapter";
import DustHelpers from "helpers/DustHelpers";
import { PhaseType } from "common/types";

export interface IMainStore {
  dustInfoList: DustInfo[];
  phase: PhaseType;
  login: (uuid: string, fcmToken: string) => Promise<boolean>;
  loadDustInfoList: (long: number, lat: number) => void;
}

@autobind
export class MainStore implements IMainStore {
  private uuid: string | null = null;

  @observable
  dustInfoList: DustInfo[] = [];
  @observable
  phase: PhaseType = 1;

  @action
  public async login(uuid: string, fcmToken: string): Promise<boolean> {
    const res = await login(uuid, fcmToken);

    if (res) {
      this.uuid = uuid;
      localStorage.setItem("dust_uuid", this.uuid);
      return true;
    } else {
      return false;
    }
  }

  @action
  public async loadDustInfoList(long: number, lat: number) {
    const { data } = await getDustInfoList(long, lat);
    const currentDustInfo = data;

    if (currentDustInfo) {
      this.phase = DustHelpers.getPhase(currentDustInfo.dust.PM10);
      // this.dustInfoList.push(currentDustInfo);

      this.dustInfoList = [currentDustInfo];
    }
  }
}



