import * as React from "react";
import styled from "styled-components";

import { autobind } from "core-decorators";
import { inject, observer } from "mobx-react";
import { Provider } from "mobx-react";
import { stores } from "./modules";
import { IMaskStore } from "./modules/store/MaskStore";
import { textColor } from "assets/colors";

import MaskButton from "./organisms/MaskButton";
import UsageStateSection from "./organisms/UsageStateSection";
import BackIconImg from "assets/img/back_icon.png";
import DeleteIconImg from "assets/img/delete_icon.png";

type Props = {
  MaskStore?: IMaskStore;
};

@inject("MaskStore")
@observer
@autobind
class MaskPage extends React.Component<Props> {
  state = {
    isMaskRegistered: false
  };

  render() {
    return (
      <Container>
        <HeaderContainer>
          <BackButton src={BackIconImg} />
          <DeleteButton src={DeleteIconImg} />
        </HeaderContainer>
        <SubContainer>
          <UsageStateSection isMaskRegistered={this.state.isMaskRegistered}/>
          <MaskButton isMaskRegistered={this.state.isMaskRegistered} status={1} />
          <MaskInfoContainer>
            <MaskInfoTitle>  ?</MaskInfoTitle>
            <MaskInfoDiscription>
                      
                      
                 
            </MaskInfoDiscription>
          </MaskInfoContainer>
        </SubContainer>
      </Container>
    );
  }
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: #eaeaea;
`;

const HeaderContainer = styled.div`
  margin-top: 1.25rem;
  display: flex;
  justify-content: space-between;
`
const BackButton = styled.img`
  margin-left: 0.625rem;
  width: 36px;
  height: 36px;
`;

const DeleteButton = styled.img`
  margin-right: 0.625rem;
  width: 36px;
  height: 36px;
`;

const SubContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7.3125rem;
`;

const MaskInfoContainer = styled.div`
  margin-top: 7.375rem;
  background-color: #d8d8d8;
  padding-top:1.3rem;
  width: 100%;
  padding-bottom: 1.3rem;
`

const MaskInfoTitle = styled.p`
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
  color: ${textColor};
`;

const MaskInfoDiscription = styled.p`
  margin-top: 0.9375rem;
  margin-bottom: 1.625rem;
  text-align: center;
  color: ${textColor};
  font-size: 10px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
  padding-left: 1.875rem;
  padding-right: 1.875rem;
`;

class MaskPageWithStore extends React.Component<Props> {
  public render() {
    return (
      <Provider {...stores}>
        <MaskPage />
      </Provider>
    );
  }
}

export default MaskPageWithStore;



