import * as React from "react";
import styled from "styled-components";

import { getDetailInfoColor } from "assets/colors";
import { PhaseType } from "common/types";

type Props = {
  phase: PhaseType;
};

const DetailInfo: React.FC<Props> = (props: Props) => {
  switch (props.phase) {
    case 1:
      return (
        <Container>
          <Level></Level>
          <Gage phase={props.phase} />
          <Range>0 ~ 30</Range>
        </Container>
      );
    case 2:
      return (
        <Container>
          <Level></Level>
          <Gage phase={props.phase} />
          <Range>31 ~ 80</Range>
        </Container>
      );
    case 3:
      return (
        <Container>
          <Level></Level>
          <Gage phase={props.phase} />
          <Range>81 ~ 150</Range>
        </Container>
      );
    case 4:
      return (
        <Container>
          <Level> </Level>
          <Gage phase={props.phase} />
          <Range>151 ~</Range>
        </Container>
      );
  }
};

const Container = styled.div`
  width: 24.5%;
`;

const Level = styled.p`
  text-align: center;
  font-size: 10px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
`;

const Gage: React.ComponentType<any> = styled.div`
  background: ${(props: any) => {
    if (props.phase) {
      return getDetailInfoColor(props.phase);
    } else {
      return "#ffffff";
    }
  }};
  width: 100%;
  height: 6px;
  margin: 3px 0;
`;

const Range = styled.p`
  text-align: center;
  font-size: 10px;
  font-weight: 500;
  font-family: "Noto Sans KR", sans-serif;
`;

export default DetailInfo;



