import React from "react";
import styled from "styled-components";
import { autobind } from "core-decorators";
import { withRouter } from "react-router";
import { RouteComponentProps } from "react-router";

import WeatherCard from "./WeatherCard";
import DustInfo from "../modules/model/DustInfo";
import { PhaseType } from "common/types";
import DustHelpers from "helpers/DustHelpers";
import { strokeColor } from "assets/colors";

type Props = RouteComponentProps & {
  onScrollAction: (currentScrollPos: number) => void;
  phase: PhaseType;
  dustInfoList: DustInfo[];
};

@autobind
class CardSection extends React.Component<Props> {
  private containerRef = React.createRef<any>();

  constructor(props: Props) {
    super(props);
  }

  render() {
    return (
      <Container ref={this.containerRef}>
        {this.props.dustInfoList.map((dustInfo: DustInfo, idx: number) => {
          const phase = DustHelpers.getPhase(dustInfo.dust.PM10);
          return (
            <WeatherCard
              key={idx}
              isCurrentLocation={idx === 0}
              location={dustInfo.dust.MSRSTE_NM}
              dustState={DustHelpers.getPhaseString(phase)}
              fineDust={dustInfo.dust.PM10}
              ultrafineDust={dustInfo.dust.PM25}
              phase={this.props.phase}
              description={DustHelpers.getDescription(phase)}
            />
          );
        })}
        <AddButton
          onClick={() => {
            this.props.history.push("/search");
          }}
        >
           
        </AddButton>
      </Container>
    );
  }

  componentDidMount() {
    this.onScrollEvent();
  }

  componentWillUnmount() {
    this.offScrollEvent();
  }

  onScrollEvent() {
    this.containerRef.current!.addEventListener(
      "scroll",
      this.onScrollAction,
      false
    );
  }

  offScrollEvent() {
    this.containerRef.current!.removeEventListener(
      "scroll",
      this.onScrollAction,
      false
    );
  }

  onScrollAction() {
    const currentScrollPos = this.containerRef.current!.scrollTop;
    this.props.onScrollAction(currentScrollPos);
  }
}

const Container = styled.div`
  display: grid;
  grid-row-gap: 0.625rem;
  width: calc(100% - 1.25rem);
  height: calc(100% - 8.4375rem);
  margin: 8.4375rem 0.625rem 6.25rem 0.625rem;
  overflow-y: scroll;
  padding-top: 8.4375rem;
  padding-bottom: 8.4375rem;
`;

const AddButton = styled.button`
  position: relative;
  width: 100%;
  height: 9.125rem;
  border: solid 1px ${strokeColor};
  background-color: rgba(255, 255, 255, 0.3);
  font-family: "Noto Sans KR", sans-serif;
`;

export default withRouter(CardSection);



