export const backgroundColor = "#d8d8d8";
export const boxColor = "#eaeaea";
export const floatingBoxColor = "#b7c6d1";
export const subTitleColor = "#898989";

export const blockColor = "#b7c6d1";

export const textColor = "#1a1b1d";
export const strokeColor = "#979797";

export const firstPhaseColor = "#edf2f6";
export const secondPhaseColor = "#f4eedc";
export const thirdPhaseColor = "#d0ceca";
export const fourthPhaseColor = "#454545";

export const getMaskButtonColor = (phase: 1 | 2 | 3 | 4) => {
  if (phase === 1) {
    return "#e4b788";
  } else if (phase === 2) {
    return "#e4b788";
  } else if (phase === 3) {
    return "#ca9865";
  } else {
    return "#ca9865";
  }
};

export const getGageBlockColor = (phase: 1 | 2 | 3 | 4) => {
  if (phase === 1) {
    return "#b7c6d1";
  } else if (phase === 2) {
    return "#b7c6d1";
  } else if (phase === 3) {
    return "#e4b788";
  } else {
    return "#e4b788";
  }
};

export const getBackgroundColor = (phase: 1 | 2 | 3 | 4) => {
  if (phase === 1) {
    return "#edf2f6";
  } else if (phase === 2) {
    return "#f4eedc";
  } else if (phase === 3) {
    return "#d0ceca";
  } else {
    return "#454545";
  }
};

export const getDetailInfoColor = (phase: 1 | 2 | 3 | 4) => {
  if (phase === 1) {
    return "#ffffff";
  } else if (phase === 2) {
    return "#f4eedc";
  } else if (phase === 3) {
    return "#a3a29f";
  } else {
    return "#454545";
  }
};



