import { observable, action } from "mobx";
import { autobind } from "core-decorators";
import Mask from "../model/Mask";

export interface IDetailStore {
  mask: Mask;
  loadStartTime: () => void;
}

@autobind
export class DetailStore implements IDetailStore {
  @observable
  mask: Mask = new Mask();

  @action
  public async loadStartTime() {
    this.mask = new Mask();
  }
}



