import React from "react";
import styled from "styled-components";

import MaskButton from "../molecules/MaskButton";
import { PhaseType } from "common/types";

type Props = {
  isExpandedMode: boolean;
  phase: PhaseType;
};

const TitleSection: React.FC<Props> = props => {
  return (
    <Container>
      <Title
        isLastPhase={props.phase === 4}
        isExpandedMode={props.isExpandedMode}
      >
        
      </Title>
      <SubTitle
        isLastPhase={props.phase === 4}
        isExpandedMode={props.isExpandedMode}
      >
        
      </SubTitle>
      <MinimizedTitle
        isLastPhase={props.phase === 4}
        isExpandedMode={props.isExpandedMode}
      >
        
      </MinimizedTitle>
      <MinimizedSubTitle
        isLastPhase={props.phase === 4}
        isExpandedMode={props.isExpandedMode}
      >
        
      </MinimizedSubTitle>
      <MaskButton phase={props.phase} isExpandedMode={props.isExpandedMode} />
    </Container>
  );
};

const Container = styled.div`
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  height: 8.4375rem;
  transition: height 0.5s;
  z-index: 10;
`;

const Title: React.ComponentType<any> = styled.p`
  font-size: 30px;
  margin-top: 6.25rem;
  opacity: ${(props: any) => (props.isExpandedMode ? 1 : 0)};
  transition: opacity 0.3s;
  color: ${(props: any) => (props.isLastPhase ? "white" : "#111111")};
`;

const SubTitle: React.ComponentType<any> = styled.p`
  font-size: 18px;
  margin-top: 0.625rem;
  opacity: ${(props: any) => (props.isExpandedMode ? 0.63 : 0)};
  transition: opacity 0.3s;
  color: ${(props: any) => (props.isLastPhase ? "white" : "#111111")};
`;

const MinimizedTitle: React.ComponentType<any> = styled.p`
  position: absolute;
  font-size: 24px;
  top: 5rem;
  left: 1.25rem;
  opacity: ${(props: any) => (props.isExpandedMode ? 0 : 1)};
  transition: opacity 0.5s;
  color: ${(props: any) => (props.isLastPhase ? "white" : "#111111")};
`;

const MinimizedSubTitle: React.ComponentType<any> = styled.p`
  position: absolute;
  font-size: 18px;
  top: 5.1875rem;
  left: 6.25rem;
  opacity: ${(props: any) => (props.isExpandedMode ? 0 : 0.63)};
  transition: opacity 0.5s;
  color: ${(props: any) => (props.isLastPhase ? "white" : "#111111")};
`;

export default TitleSection;



