import React from "react";
import styled from "styled-components";
import { autobind } from "core-decorators";
import { inject, observer } from "mobx-react";
import { Provider } from "mobx-react";

import { stores } from "./modules";
import { IMainStore } from "./modules/store/MainStore";
import BottomFloatingBox from "./organisms/BottomFloatingBox";
import CardSection from "./organisms/CardSection";
import TitleSection from "./organisms/TitleSection";
import AnimationBackground from "./molecules/AnimationBackground";

type Props = {
  MainStore?: IMainStore;
};

@inject("MainStore")
@observer
@autobind
class MainPage extends React.Component<Props> {
  state = {
    prevScrollpos: window.pageYOffset,
    isExpandedMode: true
  };

  render() {
    const phase = this.props.MainStore!.phase;
    const dustInfoList = this.props.MainStore!.dustInfoList;

    return (
      <Container>
        <AnimationBackground phase={phase} />
        <TitleSection
          phase={phase}
          isExpandedMode={this.state.isExpandedMode}
        />
        <CardSection
          phase={phase}
          onScrollAction={this.onScrollAction}
          dustInfoList={dustInfoList}
        />
        <BottomFloatingBox duration={6} />
      </Container>
    );
  }

  async componentDidMount() {
    const onLogin = await this.props.MainStore!.login("test", "test");

    if (onLogin) {
      await this.props.MainStore!.loadDustInfoList(127.030507, 37.49917);
    }
  }

  onScrollAction(currentScrollPos: number) {
    if (currentScrollPos <= 80) {
      this.setState({
        prevScrollpos: currentScrollPos,
        isExpandedMode: true
      });
    } else {
      this.setState({
        prevScrollpos: currentScrollPos,
        isExpandedMode: false
      });
    }
  }
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  overflow: hidden;
`;

class MainPageWithStore extends React.Component<Props> {
  public render() {
    return (
      <Provider {...stores}>
        <MainPage />
      </Provider>
    );
  }
}

export default MainPageWithStore;



